<template>
<html>
    <header>this is good detail components!</header>
    <button v-on:click="goBack">goback</button>
    <span>{{$route.params.id}}</span>
</html>
</template>
<script>
export default {
  methods:{
      goBack:function(){
          window.history.length>1
          ?this.$router.go(-1)
          :this.$router.push('/')

      }
  }
}
</script>




