<template>
  
  <div>
    <Header></Header>
    <h1>Welcome to SellingCell!</h1>
    <span>{{message}}</span>
    <router-link to="/good/1">Go to good_detail_page</router-link>
    <button v-on:click='goto_detail'>with params</button>
  </div>
</template>
<script>
 import Header from '../components/header.vue'
 import GoodsList from '../components/goods.vue'

export default {
  components:{Header,GoodsList},
  data(){
    return {
        message :new Date().toLocaleString()
    }
  },
  created(){
    this.$api.get('login',null,response=>{
      console.log(response)
    },null)
  },
  methods:{
   goto_detail:function(){
     this.$router.push({name:'good_detail',params:{id:5,age:21}})
   }
  }
}
</script>



