import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import Content from '@/page/content'
import Good from '@/components/good'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   component:Index
    // },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path:'/content/:id',
      component:Content,
      name: 'content' 
    },
    {
      path:'/good/:id',
      component:Good,
      name: 'good_detail'
    }
  ]
})



